* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
  width: 100%;
}

button {
  position: relative;
  overflow: hidden;
  background-color: transparent;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  box-shadow: 0 0.3rem rgba(121, 121, 121, 0.65);
  cursor: pointer;
  font-size: 1.5rem;
  padding: 0.25rem 0.7rem;
  z-index: 1;
  transition: color 0.7s transform 0.1s box-shadow 0.1s;
}

button:hover {
  color: #fff;
}

button:active {
  transform: translate(0, 0.3rem);
  box-shadow: 0 0.1rem rgba(255, 255, 255, 0.65);
  filter: drop-shadow(0 0 10px #000) brightness(2);
  text-shadow: 0 0 10px #fff;
  color: #f38a38;
}

.twitter-btn {
  transition: all 0.1s;
}

.twitter-btn:active {
  color: #38a1f3;
}

button:before {
  position: absolute;
  content: "";
  top: 0;
  left: -100%;
  height: 100%;
  width: 100%;
  background-color: #000;
  border-radius: 5px;
  z-index: -1;
  transition: all 0.7s;
}

button:hover:before {
  left: 0;
}

