.quote-wrapper {
  width: 90%;
  max-width: 900px;
  margin: auto;
  padding: 20px 30px;
  border-radius: 10px;
  background-color: 255 255 255 0.4;
  box-shadow: 0 10px 10px 10px rgba(0, 0, 0, 0.2);
}

.quote-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 16px;
  width: 100%;
}

.quote-text {
  font-size: 2.5rem;
  line-height: 1.5;
}

.long-quote {
  font-size: 2rem;
}

.fa-quote-left {
  font-size: 4rem;
}

.author {
  font-size: 1.75rem;
  font-weight: 400;
  font-style: italic;
}

.buttons {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
}

.hide {
  display: none;
}

/* ===== Page Loader ===== */

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #333;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
  /* margin: auto; */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media only screen and (min-width: 768px) {
  .quote-text {
    font-size: 2.75rem;
  }

  .author {
    font-size: 2rem;
  }

  button {
    font-size: 1.3rem;
  }
}
